import React, { Component, Fragment } from 'react';
import { AboutSection } from "./components/Section/AboutSection";
import { LinkSection } from "./components/Section/LinkSection";
import { WorkSection } from "./components/Section/WorkSection";
import { GameSection } from "./components/Section/GameSection";
import { BigText } from "./components/BigText";
import { EducationSection } from "./components/Section/EducationSection";
import { Intermodal } from "./components/Intermodal";
import { ThemeButton } from "./components/ThemeButton";
import { Header } from "./components/Header";
import { Footer } from "./components/Footer";

export class App extends Component {
    
    state = {
        dark_mode : false,
        intermodal : {
            isVisible : true,
            isLoaded : false,
            state_num : 0
        }
    }
    
    componentDidMount() {
        setTimeout(this.checkTime, 200)
    }
    
    checkTime = () => {
        let d = new Date()
        let nextState = d.getHours() < 7 || d.getHours() > 17;
        if ( nextState !== this.state.dark_mode ) {
            this.swapTheme();
        } else {
            setTimeout(this.advanceState, 1000)
        }
        setTimeout(this.checkTime,60*60*1000)
    }
    
    swapTheme = () => {
        this.setState({
            intermodal : {
                isVisible : true,
                isLoaded : false,
                state_num : 0
            }
        })
        setTimeout(() => {
            this.setState({
                dark_mode : !this.state.dark_mode
            })
        }, 1000)
        setTimeout(this.advanceState, 2000)
    }
    
    advanceState = () => {
        const { intermodal } = this.state;
        if ( intermodal.state_num === 0 ) {
            this.setState({
                intermodal : {
                    isLoaded : true,
                    isVisible : true,
                    state_num : 1
                }
            })
            setTimeout(this.advanceState, 1500)
        } else if ( intermodal.state_num === 1 ) {
            this.setState({
                intermodal : {
                    isLoaded : true,
                    isVisible : false,
                    state_num : 2
                }
            })
            setTimeout(this.advanceState, 1000)
        } else if ( intermodal.state_num === 2 ) {
            this.setState({
                intermodal : {
                    isLoaded : false,
                    isVisible : false,
                    state_num : 3
                }
            })
        }
    }
    
    
    render() {
        const { intermodal, dark_mode } = this.state;
        
        return (
            <Fragment>
                <Intermodal dark={dark_mode} {...intermodal} />
                <div className={'app' + (dark_mode ? ' dark-theme' : '')}>
                    <ThemeButton
                        swapTheme={this.swapTheme}
                        current={dark_mode}
                    />
                    
                    <Header />
                    
                    <AboutSection />
                    <BigText text={''} marginTop={true} />
                    <LinkSection />
                    <BigText text={''} marginTop={true} marginBottom={true} />
                    <WorkSection />
                    
                    <GameSection />
                    
                    <BigText text={'education'} marginTop={true} marginBottom={true} />
                    <EducationSection />
                    
                    <Footer />
                    
                </div>
            </Fragment>
        );
    }
}

