import React from 'react';
import picture from '../../assets/pic1_small.jpg'
import { profile } from "../../utils/Data";
import { GlassCard } from "../Cards/GlassCard";
import { Picture } from "../Picture";
import { ParagraphStopper } from "../ParagraphStopper";

export function AboutSection() {
    return (
        <div className={'section about'}>
            
            <div className={'cards-side'}>
                <GlassCard
                    title={'About me'}
                    innerClass={'column-style'}
                >
                    <p>
                        {profile.description}
                    </p>
                    <ParagraphStopper />
                </GlassCard>
            </div>
            
            <Picture picture={picture} className={'small'} />
        </div>
    );
}
