import React from 'react';
import { GlassCard } from "./GlassCard";

export function WorkCard( { select, selected, item, i } ) {
    return <div
        className={'work-card-container ' + (selected === i ? 'sel' : '')}
        onClick={() => select(selected === i ? -1 : i)}
    >
        
        <GlassCard>
            <img
                src={item.img}
                alt={item.name}
            />
        </GlassCard>
        
        <div className={'description'}>
            {item.description}
        </div>
        
    </div>
}
