import React from 'react';
import { GlassCard } from "./GlassCard";

export function LinkCard( { title, link } ) {
    return (
        <GlassCard
            title={title}
            innerClass={'button-style'}
        >
            <a
                className={'button'}
                href={link}
                target={'_newtab'}
            >
                Click here
            </a>
        </GlassCard>
    );
}

