import React  from 'react';
import { games } from "../../utils/Data";
import { GameCard } from "../Cards/GameCard";
import picture from "../../assets/pic3_small.jpg";
import { Picture } from "../Picture";

export function GameSection() {
    return (
        <div className={'section games'}>
            <Picture picture={picture} className={'large enlarge'}/>
    
            <div className={'cards-side'}>
                {
                    games.map(
                        ( item, i ) => {
                            return <GameCard
                                key={i}
                                link={item.link}
                                team_img={item.team_pic}
                                game_img={item.title_pic}
                            />
                        }
                    )
                }
            </div>
        </div>
    );
}
