import React, { useState } from 'react';
import { work } from "../../utils/Data";
import { WorkCard } from "../Cards/WorkCard";
import { BigText } from "../BigText";

export function WorkSection() {
    const [ selected, select ] = useState(-1)
    
    return (
        <div className={'work'}>
            <div className={'pocket'}>
                <div className={'border-element-container top-border'}>
                    <div className={'border-element'} />
                </div>
                
                <Pocket selected={selected} select={select} />
                
                <div className={'border-element-container bottom-border'}>
                    <div className={'border-element'} />
                </div>
            </div>
            <BigText text={'professional work'} marginBottom={true} />
        </div>
    );
}

function Pocket( { selected, select } ) {
    return <div className={'card-holder'}>
        <div className={'pocket-side'}>
            {
                work.map(( item, i ) => {
                    return <WorkCard
                        item={item}
                        key={i}
                        i={i}
                        selected={selected}
                        select={select}
                    />
                })
            }
        </div>
        
        <div className={'pocket-side'} />
    </div>
}
