import React from 'react';
import { GlassCard } from "./GlassCard";

export function GameCard( { title, team_img, game_img, link } ) {
    return (
        <GlassCard
            title={title}
            innerClass={'img-style'}
        >
            <img
                src={team_img}
                alt={'Team'}
            />
            <img
                src={game_img}
                alt={'Game'}
            />
            <a
                className={'button'}
                href={link}
                target={'_newtab'}
            >
                Click here
            </a>
        </GlassCard>
    );
}
