import React from 'react';

export function Intermodal( { dark, isLoaded, isVisible } ) {
    return (
        <div
            className={'intermodal' + (isVisible ? '' : ' hidden') + (dark ? ' dark' : '')}
            onClick={( e ) => {
                e.stopPropagation()
            }}
        >
            {/*<div className={'text'}>*/}
            {/*    Loading ...*/}
            {/*</div>*/}
            <div className={'filled-stopper' + (isLoaded ? ' loaded' : '')} />
        </div>
    );
}

