import React from 'react';

export function GlassCard( { className = '', innerClass = '', title, children } ) {
    return (
        <div className={'glass-card ' + className}>
            <div className={'card-title'}>
                {title}
            </div>
            <div className={'card-front ' + innerClass} >
                {children}
            </div>
        </div>
    );
}

